.magnifier{position:absolute;margin:auto;display:block}.magnifier-image{cursor:auto}.magnifying-glass:before{content:"⚀";color:#f0f8ff;width:50px;height:50px;font-size:50px;line-height:45px;text-align:center;display:block;transform:translate(50px, 50px) rotate(45deg)}.point{position:absolute;width:16px;height:16px;border-radius:16px;background-color:#a5d8a7}.markPlace{height:100%;margin:auto}